async function afterInstall() {
  console.log("--------afterInstall");

  let savedGreenhubExt = await browser.storage.local.get("greenhubExt");
  // console.log("savedGreenhubExt", savedGreenhubExt);
  if (
    savedGreenhubExt.greenhubExt &&
    Object.keys(savedGreenhubExt.greenhubExt).length > 0 &&
    "uid" in savedGreenhubExt.greenhubExt &&
    savedGreenhubExt.greenhubExt.uid !== undefined
  ) {
    await browser.storage.local.set({ isNewUser: false });
    if (savedGreenhubExt.greenhubExt.uid.indexOf("-") != -1) {
      let newUid = await makeNewUid();
      if (newUid) {
        savedGreenhubExt.greenhubExt.uid = newUid;
        const manifestData = browser.runtime.getManifest();
        greenhubExt.version = manifestData.version;
        greenhubExt.sharedDate = 0;
        await browser.storage.local.set({ greenhubExt: greenhubExt });
      }
    }
  } else {
    console.log("not saved ui,generate new one");
    const manifestData = browser.runtime.getManifest();
    // 设置 id 和版本
    greenhubExt.version = manifestData.version;
    greenhubExt.uid = await makeNewUid();
    greenhubExt.sharedDate = 0;
    await browser.storage.local.set({ greenhubExt: greenhubExt });
  }
  // console.log("greenhubExt", greenhubExt);
}
//通过FingerprintJS生成uid
async function makeNewUid() {
  const fp = await fpPromise;
  const result = await fp.get();
  if (result.visitorId) {
    // console.log("greenhubExt.uid---------新生成------", result.visitorId);
    return result.visitorId;
  }
  return "";
}

