// 打开插件后要做的事
async function initGreenhub() {
  console.log("initGreenhub------------");
  // 加载用户 id, 版本号, 退出浏览器时保存的连接状态
  let savedGreenhubExt = await browser.storage.local.get("greenhubExt");
  if (
    savedGreenhubExt.greenhubExt &&
    Object.keys(savedGreenhubExt.greenhubExt).length > 0 &&
    "uid" in savedGreenhubExt.greenhubExt &&
    savedGreenhubExt.greenhubExt.uid !== undefined
  ) {
    // 重新读取最新版本号
    savedGreenhubExt.greenhubExt.version =
      browser.runtime.getManifest().version;
  }
  await browser.storage.local.set({
    greenhubExt: savedGreenhubExt.greenhubExt,
  });
  if (savedGreenhubExt && savedGreenhubExt.greenhubExt) {
    greenhubExt = savedGreenhubExt.greenhubExt;
  }

  // 如果有连接则断开
  if (greenhubExt.connection) {
    disconnect().then();
  }
}
initGreenhub().then();

let pendingRequests = [];
// 如果请求已完成，则直接删除
function completed(details) {
  const index = pendingRequests.indexOf(details.requestId);
  if (index > -1) {
    pendingRequests.splice(index, 1);
  }
}

// 监听认证请求函数
function onAuth(details) {
  // todo: 这里后续可以增加对 details 中 challenger.host challenger.port 的校验，如果是当前节点再提供验证
  // note: firefox 中 details 的 challenger.host 是 origin host, 判断需要注意
  console.debug("authing: ", details);
  console.debug(pendingRequests);
  if (details.isProxy) {
    if (pendingRequests.indexOf(details.requestId) !== -1) {
      return {
        cancel: true,
      };
    }
    pendingRequests.push(details.requestId);
    return {
      authCredentials: {
        username: greenhubExt.connection.auth.username,
        password: greenhubExt.connection.auth.password,
      },
    };
  }
}

// 设置代理
function setProxy(proxy) {
  const config = {
    mode: "pac_script",
    pacScript: {
      data: genPac(proxy.hostWithoutScheme, proxy.port, proxy.protocol),
    },
  };
  // browser.webRequest.onAuthRequired.removeListener(onAuth)
  // browser.webRequest.onCompleted.removeListener(completed)
  // browser.webRequest.onErrorOccurred.removeListener(completed)
  browser.webRequest.onAuthRequired.addListener(
    onAuth,
    { urls: ["<all_urls>"] },
    ["blocking"]
  );
  browser.webRequest.onCompleted.addListener(completed, {
    urls: ["<all_urls>"],
  });
  browser.webRequest.onErrorOccurred.addListener(completed, {
    urls: ["<all_urls>"],
  });
  chrome.proxy.settings.set(
    {
      value: config,
      scope: "regular",
    },
    () => {}
  );
}

// 移除代理设置
function unsetProxy() {
  chrome.proxy.settings.set(
    {
      value: {
        mode: "system",
      },
      scope: "regular",
    },
    () => {}
  );
  chrome.webRequest.onAuthRequired.removeListener(onAuth);
  browser.webRequest.onCompleted.removeListener(completed);
  browser.webRequest.onErrorOccurred.removeListener(completed);
}

// 连接代理
async function connect(data) {
  console.debug("connecting..", data);

  // 检测是否已经在使用
  if (greenhubExt.connection) {
    if (data.ip === greenhubExt.connection.ip) {
      return false;
    }
    await disconnect();
  }
  let {
    // eslint-disable-next-line no-unused-vars
    ip,
    host,
    capacity,
    current,
  } = data;
  // 检测代理负载
  if (current >= capacity) {
    return false;
  }
  // 获取连接信息
  let rst = await axios.post(`${host}/connect`, {
    f: greenhubExt.uid,
    v: greenhubExt.version,
  });
  if (rst.status !== 200) {
    return false;
  }
  startCountDown();
  console.debug("connecting to proxy: ", rst);
  const resp = rst.data;
  // 保存连接状态
  greenhubExt.connection = {
    ...data,
    hostWithoutScheme: resp.host,
    auth: resp.auth,
    port: resp.port,
    protocol: resp.protocol,
    timestamp: new Date().getTime(),
  };
  lastConnectedProxy = data;
  browser.storage.local.set({
    greenhubExt: greenhubExt,
  });
  // 设置代理
  setProxy(greenhubExt.connection);
  // 启动 30 分钟定时器，到时间自动断开
  browser.alarms.create("disconnect", {
    // delayInMinutes: 30
    // 每一分钟触发一次
    periodInMinutes: 1,
  });
  // 设置图标为绿色已连接
  browser.browserAction.setIcon({
    path: connectPath,
  });
  return greenhubExt.connection;
}
let timer;
function startCountDown() {
  if (isGHEdgePlatform()) {
    checkAndCountDown();
  } else if (isMagicPlatform()) {
    console.log("isMagicPlatform----");
    checkAndCountDown();
  }
}

async function checkAndCountDown() {
  let connectRest = 0;
  //上次使用日期
  let lastUseData = await browser.storage.local.get({ date: "" });
  //今天日期
  let today = new Date();
  let curDate = `${today.getFullYear()}-${
    today.getMonth() + 1
  }-${today.getDate()}`;

  //检查当天是否使用过
  if (lastUseData && lastUseData.date && lastUseData.date == curDate) {
    let restTime = await browser.storage.local.get("connectRest");
    connectRest = restTime.connectRest;
  }
  // else {
  //   let connectTimeData = await browser.storage.local.get("connectMaxMinutes");
  //   let connectMaxTime = connectTimeData.connectMaxMinutes;

  //   connectRest = connectMaxTime;
  //   await browser.storage.local.set({
  //     date: curDate,
  //     connectRest: connectRest,
  //   });
  // }
  timer = setInterval(async () => {
    if (connectRest > 0) {
      connectRest -= 1;
    } else {
      // 通知其他页面统计达到限制
      browser.runtime.sendMessage({
        action: "ga_achieve_limited",
        data: {},
      });
      disconnect();
    }

    await browser.storage.local.set({
      connectRest: connectRest,
      date: curDate,
    });
  }, 60000);
}
//判断gh-edge浏览器
function isGHEdgePlatform() {
  return process.env.VUE_APP_TYPE == "GH_EDGE";
}
//判断magic
function isMagicPlatform() {
  return process.env.VUE_APP_TYPE == "MG";
}
// 断开
async function disconnect() {
  //停止倒计时
  clearInterval(timer);
  // 移除代理设置
  unsetProxy();
  // 发送断开连接请求
  if (greenhubExt.connection) {
    axios
      .post(`${greenhubExt.connection.host}/disconnect`, {
        f: greenhubExt.uid,
        v: greenhubExt.version,
      })
      .then((resp) => {
        if (resp.status !== 200) {
          console.debug("disconnect failed");
        }
      });
    // 清空连接变量
    greenhubExt.connection = null;
  }
  // 移除本地存储中保存的连接
  await browser.storage.local.set({
    greenhubExt: greenhubExt,
  });
  // 更换图标为红色版
  browser.browserAction.setIcon({
    path: disconnectPath,
  });
  // 通知其他页面 (option, popup)
  browser.runtime.sendMessage({
    action: "disconnected",
    data: {},
  });
}

// 定时器回调
browser.alarms.onAlarm.addListener((alarm) => {
  if (alarm.name === "disconnect") {
    if (greenhubExt.connection) {
      if (new Date().getTime() - greenhubExt.connection.timestamp > 1800000) {
        disconnect().then();
        browser.alarms.clear("disconnect");
      }
    }
  }
});

// 重新链接上次使用的节点
async function reconnect() {
  return connect(lastConnectedProxy);
}

// 检查连接人数
function totalConnections() {
  return Promise.resolve(totalConnectionCount);
}

// 更新连接人数, 每次进入 option 页面并加载完所有节点信息后，计算总连接用户数，并回传
function setTotalConnections(data) {
  totalConnectionCount = data;
  return totalConnections();
}

